/* global console */

export interface ITag {
  Name: string;
  Type: string;
  Address: number;
}

export interface ITagString {
  Name: string;
  Type: string;
  Address: number;
  Length: number;
}

export interface ITagBool {
  Name: string;
  Type: string;
  Address: number;
  BitAddress: number;
}

export enum TagTypeNumeric {
  Real = "REAL",
  Sint = "SINT",
  Int = "INT",
  Dint = "DINT",
  Lint = "LINT",
}

/** Tag group object */
export class TagGroup {
  Name: string;
  Type: number;
  Tags: (ITag | ITagBool | ITagString)[];
  _validTypes: string[];

  constructor(name: string) {
    this.Name = name;
    this.Type = 1;
    this.Tags = new Array();
  }
  /** Add tags to group */
  addNumericTag(name: string, type: string, address: number) {
    const newTag: ITag = {
      Name: name,
      Type: type,
      Address: address,
    };
    this.Tags.push(newTag);
  }

  addStringTag(name: string, address: number, length: number) {
    const newTag: ITagString = {
      Name: name,
      Type: "STRING",
      Address: address,
      Length: length,
    };
    this.Tags.push(newTag);
  }

  addBoolTag(name: string, address: number, bitAddress: number) {
    const newTag: ITagBool = {
      Name: name,
      Type: "BOOL",
      Address: address,
      BitAddress: bitAddress,
    };
    this.Tags.push(newTag);
  }
}

/** Represents a tag source object (file) */
export class TagSource {
  Name: string;
  TagGroups: TagGroup[];

  constructor(name: string) {
    this.Name = name;
    this.TagGroups = new Array();
  }
}

/** Datasource type enums */
export enum DataSourceType {
  Continuous = "Continuous",
  TriggeredChannels = "TriggeredChannels",
  TriggeredSet = "TriggeredSet",
  Duration = "Duration",
  FaultStack = "FaultStack",
}

/** Trigger object */
export interface Trigger {
  Tag: string;
  Type: string;
  Latch: boolean;
}

/** Data channel object */
export class DataChannel {
  Name: string;
  Tag: string;
  Trigger?: Trigger;
  ValueDictionary?: object;

  constructor(name: string, tag: string) {
    this.Name = name;
    this.Tag = tag;
  }
  /** Add value dictionary from object */
  addValueDictionary(valueDictionary: object) {
    // NEED ERROR CHECKING??
    this.ValueDictionary = valueDictionary;
  }

  /** Add self as trigger */
  addTriggerSelf(type: string) {
    const trigger: Trigger = {
      Tag: this.Tag,
      Type: type,
      Latch: false,
    };

    this.Trigger = trigger;
  }
}

/** Represents a data source object (file) */
export class DataSource {
  Name: string;
  UseLegacyDataTypes = false;
  Type: string;
  TagSource: string;
  ServerAddress: string;
  ServerPort: number;
  Database: string;
  Table: string;
  ShiftSchedule?: object;
  StaticMetadata?: object;
  DataChannels: DataChannel[];
  MetadataChannels: DataChannel[];

  constructor(
    name: string,
    sourceType: string,
    tagSource: string,
    serverAddress: string,
    dbName: string,
    table: string
  ) {
    this.Name = name;
    this.UseLegacyDataTypes = false;
    this.Type = sourceType;
    this.TagSource = tagSource;
    this.ServerAddress = serverAddress;
    this.ServerPort = 65400;
    this.Database = dbName;
    this.Table = table;
    this.DataChannels = new Array();
    this.MetadataChannels = new Array();
  }
  /** Add hard-coded shift schedule as default */
  addDefaultShiftData() {
    this.ShiftSchedule = {
      Shifts: [
        {
          Name: "Shift 1",
          Start: "07:00:00",
          End: "15:00:00",
        },
        {
          Name: "Shift 2",
          Start: "15:00:00",
          End: "23:00:00",
        },
        {
          Name: "Shift 3",
          Start: "-01:00:00",
          End: "07:00:00",
        },
      ],
    };
  }
  /** Add shift schedule data from string input
   * @todo NEED ERROR CHECKING
   */
  addShiftSchedule(shiftSchedule: string) {
    this.ShiftSchedule = JSON.parse(shiftSchedule);
  }

  /** Add data channel to object
   * @todo NEED ERROR CHECKING
   * @todo Add triggered channels work
   */
  addDataChannel(name: string, tag: string, valueDict?: object) {
    const dataChannel = new DataChannel(name, tag);
    valueDict ? dataChannel.addValueDictionary(valueDict) : null; /** @todo Verify code */

    if (this.Type == "TriggeredChannels") {
      dataChannel.addTriggerSelf("OnChange");
    }

    this.DataChannels.push(dataChannel);
  }

  /** Add metadata channel to object
   * @todo NEED ERROR CHECKING
   */
  addMetadataChannel(dataChannel: DataChannel) {
    this.MetadataChannels.push(dataChannel);
  }
}
